import React from 'react'
import drawTemplate from "../Values/drawCode";

class KeuzeMenu extends React.Component{
    constructor(){
        super()
        this.handleInputChange=this.handleInputChange.bind(this)
    }

    handleInputChange(event) {
        this.props.itemChange(()=>{return event.target.value})
    }

    render() {
        return(<div className="keuzeLijst">
            {console.log(this.props)}
            {this.props.options.map(value => {
                return(<div className="keuzeGroep">
                    <p>{value.replace(/_/g," ")}</p>
                    <input type="radio" style={{display:"inline-block"}} checked={this.props.currentChoise===value} name="template" value={value} onClick={this.handleInputChange}/>
                </div>)
            })}
        </div>)
    }
}
export default KeuzeMenu